@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Extra+Condensed:ital,wght@1,700;1,800&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif, "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  position: relative;
  letter-spacing: 0.055em;
  color: #303036;
}

h1 {
  font-size: 36px;
}

a {
  color: #303036;
  text-decoration: none;
}

li {
  list-style-type: none;
}

button {
  border: none;
}

button:active,
button:focus,
input:focus,
input:active {
  box-shadow: none;
  outline: none;
}
@media screen and (max-width: 768px) {
  * {
    font-size: 16px;
  }
  h1 {
    font-size: 28px;
  }
}
@media screen and (max-width: 480px) {
  * {
    font-size: 14px;
  }
  h1 {
    font-size: 22px;
  }
}

header{
    display: flex;
    height: 70px;
    align-items: center;
    padding: 0 30px;
    position: fixed;
    z-index: 100;
}
.header-logo{
    font-style: italic;
    font-family: 'Fira Sans Extra Condensed', sans-serif;
    font-weight: bold;
    border-bottom: 4px solid #303036;
    display: inline-block;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
@media screen and (max-width: 480px) {
    header{
        padding: 0 25px;
    }
}
.home {
  /* height: calc(100vh - 70px); */
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.home-name {
  font-weight: bold;
  font-size: 48px;
  text-align: center;
  letter-spacing: 0.075em;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.home-school {
  text-align: center;
  margin: 25px 0 30px;
}
.home ul {
  width: 240px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
}
.home img {
  width: 90%;
}
.home-year {
  position: absolute;
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  margin-bottom: 100px;
  left: 0px;
}
.home-year-line {
  width: 5px;
  height: 2px;
  background-color: #303036;
}
.home-year-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  text-align: center;
  margin: 0 10px;
  letter-spacing: 3px;
}
@media screen and (max-width: 768px) {
  .home-name {
    font-size: 36px;
  }
  .home-school {
    margin: 12px 0 18px;
  }
  .home ul {
    width: 220px;
  }
  .home img {
    width: 80%;
  }
}
@media screen and (max-width: 480px) {
    .home-name {
      font-size: 30px;
    }
    .home-school {
      margin: 12px 0 18px;
    }
    .home ul {
      width: 170px;
    }
    .home img {
      width: 60%;
    }
    .home-year {
      left: -10px;
    }
}
@media screen and (max-width: 380px) {
    .home-school {
      margin: 15px 0 20px;
    }
    .home-year {
      display: none;
    }
}

.scroll {
  position: absolute;
  bottom: 50px;
  width: 93px;
  overflow: hidden;
}
.scroll-text {
  font-family: Roboto;
  text-align: center;
  line-height: 30px;
  letter-spacing: 5px;
  -webkit-filter: drop-shadow(2px 1px 1px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(2px 1px 1px rgba(0, 0, 0, 0.25));
}
.scroll-line {
  width: 100%;
  height: 3px;
  background-color: #fdd502;
  -webkit-animation: scroll-line 1.5s ease-in-out infinite both;
          animation: scroll-line 1.5s ease-in-out infinite both;
}
@-webkit-keyframes scroll-line {
  0%,
  5% {
    width: 0px;
  }
  33%,
  66% {
    left: 0px;
    width: 100%;
  }
  95%,
  100% {
    left: 100%;
  }
}
@keyframes scroll-line {
  0%,
  5% {
    width: 0px;
  }
  33%,
  66% {
    left: 0px;
    width: 100%;
  }
  95%,
  100% {
    left: 100%;
  }
}

@media screen and (max-width: 768px) {
  .scroll {
    width: 88px;
  }
  .scroll-text {
    line-height: 25px;
  }
}
@media screen and (max-width: 480px) {
  .scroll {
    width: 80px;
  }
  .scroll-text {
    line-height: 22px;
  }
}

.work1 {
  width: 100%;
  overflow: hidden;
  border: 1px soild red;
}
.work1-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #7acfc9;
}
.work1-bg img {
